* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.menu-wraper {
  justify-content: space-between;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.menu-logo, .menu-list {
  align-items: center;
  display: flex;
}

.menu-item {
  text-transform: uppercase;
  color: #000;
  margin-right: 20px;
  padding: 23px 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
}

.menu-item:last-child {
  margin-right: 0;
}

.menu-item:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
